.dialogs {
  display: flex;
  justify-content: center;
}
.dialogs .contentWrapper {
  display: flex;
  justify-content: center;
  width: 100%;
  margin-bottom: 70px;
}

.messages_block {
  min-width: 30%;
}

.dialogsItems {
  padding: 10px;
}

.dialog {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  width: 100%;
}

.user_name {
  text-decoration: none;
  color: #27aae1;
  margin-left: 10px;
}

.imgWrapper {
  width: 80px;
  height: 80px;
}

.imgWrapper img {
  width: 100%;
  border-radius: 100%;
  height: 100%;
  object-fit: cover;
}

.messages {
  padding: 10px;
  max-height: 580px;
  overflow-y: auto;
}

.message_wrap {
  display: flex;
}

.message_avatar {
  height: 60px;
  width: 60px;
  margin-left: 10px;
}

.message_avatar img {
  width: 100%;
}

.arrow_box {
  position: relative;
  background: rgba(39, 170, 225, 0.1);
  margin-bottom: 20px;
  border-radius: 5px;
  padding: 10px;
  width: 100%;
}

.arrow_box:after {
  border-bottom: 10px solid transparent;
  border-left: 8px solid rgba(39, 170, 225, 0.1);
  border-top: 10px solid transparent;
  content: "";
  height: 0;
  right: -8px;
  position: absolute;
  top: 10px;
  width: 0;
}

.arrow_box h3 {
  color: #4d4d4d;
  font-weight: 400;
  margin: 10px 0;
}

.arrow_box p {
  color: #6d6e71;
  margin: 0 0 10px;
}

.form {
  display: flex;
  align-items: flex-end;
  margin-top: 25px;
}

.textarea_wrap,
.textarea_wrap div {
  width: 300px;
  height: 100px;
}

.textarea_wrap div textarea {
  border: 1px solid #ccc;
  box-shadow: inset 0 1px 1px rgb(0 0 0 / 8%);
  background: #fff;
  border-radius: 4px;
  color: #939598;
  width: 100% !important;
  padding: 6px 12px;
  height: 100%;
}

.btn {
  background: rgb(58, 204, 167);
  padding: 7px 25px;
  border: none;
  font-size: 14px;
  color: snow;
  font-weight: 600;
  outline: none;
  border-radius: 30px;
  cursor: pointer;
  margin-left: 20px;
}

.btn:hover {
  background: rgb(57, 182, 151);
}
