.login_container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 100px;
}

.form {
  background: #fff;
  box-shadow: 0 0 35px rgb(0 0 0 / 40%);
  padding: 20px;
  border-radius: 6px;
  position: relative;
  min-height: 480px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid transparent;
  max-width: 450px;
  width: 100%;
}

.form h1 {
  color: #27aae1;
  font-size: 35px;
  font-weight: 500;
}

.type_value {
  color: #939598;
  font-size: 13px;
  margin-bottom: 5px;
  display: block;
}

.inputs_block {
  width: 100%;
  text-align: center;
}

.inputs_block input {
  background: #fff;
  color: #939598;
  border-radius: 30px;
  box-shadow: none;
  width: 100%;
  border: 1px solid #949494;
  padding-left: 15px;
  height: 60px;
  outline: transparent;
}

.form div {
  margin-bottom: 10px;
}
.form_error,
.form_error input {
  border: 1px solid red;
  background: #fff6f6;
}

.common_error {
  color: red;
  margin-top: 0;
}

.form .remember_me {
  color: #6d6e71;
  display: flex;
  align-items: center;
  margin: 0 0 20px 0;
}

.remember_me input {
  margin: 0 10px 0 0;
}

.login_btn {
  background: #27aae1;
  padding: 7px 25px;
  border: none;
  font-size: 14px;
  border-radius: 4px;
  color: #fff;
  position: relative;
  font-weight: 600;
  outline: none;
  border-radius: 30px;
  cursor: pointer;
  letter-spacing: 1px;
}
